<template>
  <div class="addClass">
    <section class="data_section">
      <el-dialog
        width="30%"
        title=""
        :visible.sync="innerDialogVisible"
        append-to-body>
        <el-input v-model="addNewGroupName" style="width: 30%"></el-input>
        <el-button type="primary" @click="addGroupNameHandle()"></el-button>
      </el-dialog>
      <el-form :model="form">
        <el-row>
          <el-col :span="11">
            <el-form-item label=":" :label-width="formLabelWidth">
              <el-input v-model="form.name"></el-input>
            </el-form-item></el-col>
          <el-col :span="11">
            <el-form-item label=":" :label-width="formLabelWidth">
              <el-input v-model="form.target"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="IDIIL:" :label-width="formLabelWidth" style="text-align: left">
          <el-row>
            <el-col :span="6"><el-button size="mini" @click="DialogIdiilVisible = true;">IDIIL</el-button></el-col>
            <el-col :span="14"><li v-for="(item,index) in sourceList"><span>{{(index+1) + ''+item.name}}</span>
              <i style="margin-left: 5px;cursor: pointer" class="el-icon-close" @click="deleteResource(index)"></i></li></el-col>
          </el-row>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" style="text-align: left">
          <el-row><el-col :span="12">
            <el-upload ref="upload"
                       :file-list="fileList"
                       action="/api/file/upload"
                       :on-remove="handleFileRemove"
                       :auto-upload="false"
                       :on-change="changeFileHandle"
                       :on-success="uploadSuccess"
                       :on-error="uploadError"
                       :data="uploadParam"
                       :before-upload="beforeUpload"
                       :limit=5
                       multiple>
              <el-button slot="trigger" size="mini" type="primary"></el-button>
              <el-button size="mini" type="success" @click="submitUpload"></el-button>
            </el-upload>
          </el-col></el-row>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="form.commits"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <div>
            <el-transfer
              style="text-align: left;"
              v-model="form.studentGroup.nowList"
              :titles="form.studentGroup.nowGroup"
              @change="handleChange"
              :data="studentData"
              :key="type">
              <span slot-scope="{ option }">{{ option.userName }}</span>
              <el-button class="transfer-footer" slot="right-footer" type="primary" size="small" @click="addGroupData()"></el-button>
              <el-button class="transfer-footer" slot="right-footer" type="primary" size="small" @click="resize()"></el-button>
              <el-button class="transfer-footer tianjiazu" slot="right-footer" size="small" @click="innerDialogVisible = true"></el-button>
            </el-transfer>
            <el-collapse v-model="activeNames" style="text-align: left">
              <el-collapse-item  v-for="(group,index) in form.studentGroup.groups" :name="index" :key="index">
                <template slot="title">{{group.name}}
                  <el-button @click.stop="editGroup(group,index)" style="margin-left: 10px" size="mini" type="primary" icon="el-icon-edit" circle></el-button>
                </template>
                <el-tag
                  v-for="(item,index) in group.studentItem"
                  :key="index"
                >
                  {{item.userName}}
                </el-tag>
              </el-collapse-item>
            </el-collapse>
          </div>
        </el-form-item>
      </el-form>
      <div style="margin-top: 50px;">
        <el-button type="primary" @click="submitAllData"> </el-button>
      </div>
    </section>
    <el-dialog
      width="60%"
      title=""
      :visible.sync="DialogIdiilVisible"
      :close-on-click-modal="false"
      append-to-body
      v-if='DialogIdiilVisible'>
      <select-class v-on:selectClassHandle="selectClassHandle"></select-class>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import headTop from '../../components/headTop.vue'
  import selectClass from '../../components/selectClassDom.vue'
  import classData from '../../data/classlist'
  import {getNowFormatDate,filterWebUrl,toJson} from '../../config/methods'
  import {setStore,getStore} from '../../config/publicMethod'
  import {uploadFile} from '../../api/upload'
  import {addClassListInfo,updateClassListInfo} from '../../api/classes'
  export default {
    props:['data','type','addFlag'],
    data(){
      let classData1 = JSON.stringify(classData);
      return {
        studentData: [],
        studentAllData: [],// 
        direction:'',//
        activeNames: ['1'],
        innerDialogVisible:false,
        DialogIdiilVisible:false,
        formLabelWidth: '90px',
        addNewGroupName:'',
        form: JSON.parse(classData1).classList,
        sourceList:[],//idiillist
        fileList:[],
        uploadParam:{
          username:JSON.parse(getStore('userInfo')).userName
        }
      }
    },
    computed: {
      sourceData() {
        let changeValue = this.form.studentGroup.nowList;
        return this.studentData.filter(item => changeValue.indexOf(item.key) === -1);
      },
    },
    watch:{
      // 
      addFlag:function(newValue,oldValue){
        console.error("watch -----",newValue,oldValue)
//        let classData1 = JSON.stringify(classData);
//        this.form = JSON.parse(classData1).classList
      },
      $route:function(to,from){
        console.error("watch --$route--11-",to,from)
      },
      '$route' (to, from) {
        console.error("watch --$route--22-",to,from)
      },
      beforeRouteUpdate (to, from, next) {
        console.error("watch --beforeRouteUpdate--33-",to,from)
      }
    },
    components:{
      headTop,
      selectClass
    },
    mounted(){
      const Wh = $(window).height();
      console.log('222222222222--->',this.data,this.type)
      fetch('static/ClassUserList.json').then((response)=>{
        return response.json();
      }).then((res)=>{
        if(this.type == 'update'){
          this.form.studentGroup = toJson(this.data.group_student_Info);
          this.sourceList = toJson(this.data.source);//idiillist
          this.fileList = toJson(this.data.otherSource);//
          this.form.otherSource = toJson(this.data.otherSource);//
          this.form.source = toJson(this.data.source);//
          this.form.name = this.data.name;//
          this.form.target = this.data.target;//
          this.form.commits = this.data.commits;//
          this.form.courseId = this.data.courseId;//
        }else {
          this.studentData = res.StudentList;
          this.studentAllData = res.StudentList;
          console.warn(":::::",this.studentData)
        }
      }).catch(()=>{
        console.error("")
      })
    },
    methods: {
      handleChange(value, direction, movedKeys) {
        console.log("studentData", this.studentData);
        console.log("value", value);
        console.log("direction", direction);
        console.log("movedKeys", movedKeys);
        this.direction = direction;
        this.form.studentGroup.nowList = value;
      },
      //  --
      addGroupData(){
        let form = this.form;
        let endnowList = this.filterNowList(form.studentGroup.nowList);//
        let nowgroupname = form.studentGroup.nowGroup[1];//
        let group = form.studentGroup.groups;//
        if($.inArray(nowgroupname,form.studentGroup.groupArray) != -1){//
          for(let ii in group){
            if(group[ii].name == nowgroupname){// 
              form.studentGroup.groups[ii].studentItem = form.studentGroup.groups[ii].studentItem.concat(endnowList);
            }
          }
        }else {
          form.studentGroup.groupArray.push(nowgroupname)
          form.studentGroup.groups.push({
            name:nowgroupname,
            studentItem : endnowList
          })
        }
        this.studentData = this.sourceData;//  
      },
      // 
      resize(){
        console.log("--",this.form.studentGroup,this.studentAllData);
        this.form.studentGroup = {
          groupArray:[],//
          nowGroup:['',''],//
          nowList:[],//
          nowStudentData:[],
          groups:[]
        }
        this.studentData = this.studentAllData;//
      },
      //
      editGroup(item,index){
        console.log("",item.name,index);
        this.form.studentGroup.nowGroup.splice(1,1,item.name);//vuesplice
        let studentitem = this.form.studentGroup.groups[index].studentItem;//
        let nowList=[];
        for(let ii in studentitem){
          nowList.push(studentitem[ii].key)
        }
        this.form.studentGroup.nowList = nowList;
        this.studentData = this.studentData.concat(studentitem);
        this.form.studentGroup.groups[index].studentItem = [];// 
        console.log(this.form.studentGroup.nowList,this.studentData)
      },
      //
      addGroupNameHandle(){
        if($.inArray(this.addNewGroupName,this.form.studentGroup.groupArray) != -1){//
          return this.$alert('')
        }
        this.innerDialogVisible = false;
        this.form.studentGroup.nowList = [];//
        this.form.studentGroup.nowGroup.splice(1,1,this.addNewGroupName);
        this.form.studentGroup.groupArray.push(this.addNewGroupName);//
        this.form.studentGroup.groups.push({
          name:this.addNewGroupName,
          studentItem : []
        })
        console.log("",this.addNewGroupName)
      },
      submitUpload() {
        const data = new FormData();
        data.append("files", this.$refs.upload.uploadFiles);
        data.append("username", JSON.parse(getStore('userInfo')).userName);
        console.log("upload--->",this.$refs.upload)
        this.$refs.upload.submit(); // "/api/file/upload"
      },
      //
      async submitAllData(){
        this.form.source = this.sourceList;
        this.form.datetime = getNowFormatDate.YY_MM_DD();
        this.form.teacherId = JSON.parse(getStore('userInfo')).userId;
        this.form.teacherName = JSON.parse(getStore('userInfo')).userName;
        this.form.subjectName = '';
        if(this.form.name=='' || this.form.target=='' || this.form.source.length==0 ){
          return this.$alert("")
        }
        console.log("",this.form,this.type)
        let classList = JSON.parse(getStore("classList"));
        classList.push(this.form);
        if(this.type == 'add'){
          const  result = await addClassListInfo(this.form)//
          setStore("classList",JSON.stringify(classList))//
          this.$message({message: '',type:'success'});
        }else if(this.type == 'update'){
          const  result = await updateClassListInfo(this.form)
          this.$message({message: '',type:'success'});
        }else {
          this.$message({message: ',^^',type:'warning'});
        }
      },
      //
      handleFileRemove(file){
        let filterNowList = this.fileList;
        for(let ii in filterNowList){
          if(filterNowList[ii].uid == file.uid){
            filterNowList.splice(ii,1)
            break;
          }
        }
        console.log("",file, this.fileList);
      },
      //
      changeFileHandle(file){
        console.log("",file, this.fileList);
      },
      beforeUpload(file){
        //
        console.log("",file);
      },
      //
      uploadSuccess(response, file, fileList){
        console.log("",response,file,fileList);
        this.form.otherSource = fileList;
        this.$message({message: '',type:'success'});
      },
      //
      uploadError(err, file, fileList){
        console.log("",err,file,fileList);
        this.$message({message: '',type:'error'});
      },
      //IDIIL
      deleteResource(index){
        this.sourceList.splice(index,1)
      },
      //
      selectClassHandle(param){
        let ClassUserInfo = JSON.parse(getStore("ClassUserList"));
        let UserInfo = JSON.parse(getStore("userInfo"));
        console.log("---",ClassUserInfo,ClassUserInfo.CenterID)
        let objParam={
          sChapterID:param.CID,
          sUnitID:param.UID,
          sChapterName:param.ChapterName,
          sUnitName:param.UnitName,
          sDirName:param.DirName,
          sCenterID : ClassUserInfo.CenterID,
          sCenterWeb : ClassUserInfo.CenterWeb,
          sMainWeb : ClassUserInfo.MainWeb,
          sAssignID : ClassUserInfo.ClassID,
          sPerformanceID : ClassUserInfo.ClassID,
          InstructorID : UserInfo.InstructorID,
          OutputType:param.selectType,
          GlobalID:param.GlobalID
        }
        let urlEnd = filterWebUrl(objParam);
        let urlJson= {
          name:param.selectUnitName,
//          url:urlEnd,
          params:objParam,
          type:"class",
          uid: new Date().getTime()
        }
        this.sourceList.push(urlJson)
        this.$message({'type':'success',message:""})
      },
      //
      filterNowList(data){
        let result = [];
        for(let ii in data){
          for(let jj in this.studentData){
            if(this.studentData[jj].key == data[ii]){
              result.push(this.studentData[jj])
            }
          }
        }
        return result
      }
    }
  }
</script>

<style scoped  lang="less" type="text/less">
  .addClass{
    .data_section {
      padding: 20px;
      margin-bottom: 40px;
      .righttable table{
        border: 1px solid #999;
        tr td{
          border-top: 0;
          border-right: 1px solid #999;
          border-bottom: 1px solid #999;
          border-left: 0;

        }
      }
      .transfer-footer {
        margin-left:10px;
        padding: 6px 5px;
      }
      .el-tag{
        margin: 0 3px;
      }
      .tianjiazu{
        float: right;
        margin: 6px 0 0 5px;
      }
    }
  }
</style>



