<template>
  <div class="classManager">
    <head-top></head-top>
    <section class="data_section">
      <addClass :data="noneData" type="add" :addFlag="addFlag" key="add"></addClass>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
  import headTop from '../../components/headTop.vue'
  import addClass from './addClass.vue'
  export default {
    data(){
      return {
        noneData:{},
        addFlag:true
      }
    },
    components:{
      headTop,
      addClass
    },
    watch:{
      // 
      addFlag:function(newValue,oldValue){
        console.error("watch -----",newValue,oldValue)
      },
      $route:function(to,from){
        console.error("watch --$route--11-",to,from)
      },
      '$route' (to, from) {
        console.error("watch --$route--22-",to,from)
      },
      beforeRouteUpdate (to, from, next) {
        console.error("watch --beforeRouteUpdate--33-",to,from)
      }
    },
    activated(){
      //  vuekeep-alive,
//      this.addFlag = !this.addFlag
//      console.log("activated--->",this.addFlag)
    },
    mounted(){
      const Wh = $(window).height();
      $(".data_section").css("height",(Wh-150)+'px');
    },
    methods: {
    }
  }
</script>

<style scoped  lang="less" type="text/less">
  .classManager{
    .data_section {
      padding: 20px;
      overflow: auto;
      margin-bottom: 40px;
    }
  }
</style>



