<template>
  <div>
    <head-top></head-top>
    <section class="classManagerList_section">
      <el-row>
        <el-col :span="4" style="margin-right: 10px;">
          <el-card class="box-card" style="height: 600px">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div v-if="classList.length>0">
              <div class="text item classlist"
                   v-for="(item,index) in classList"
                   :id="'listitem' + index"
                   :key="index"
                   :class="{ listcolor:index==currentindex}"
                   @click="clickItem(item,index)">
                {{(index+1)+''+item.name}}
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12" style="height: 100%;overflow: hidden" class="righttable">

          <el-card class="box-card" style="height: 600px">
            <div slot="header" class="clearfix">
              <span></span>
              <el-button type="warning" size="small" style="float: right;margin-left: 5px"  @click="deleteCourseConfirm(tableData,currentindex)"></el-button>
              <el-button type="warning" size="small" style="float: right;" @click="dialogEditVisible = true"></el-button>
              <el-button type="warning" size="small" style="float: right;" @click="goToClass(tableData)"></el-button>
            </div>
            <div class="text item">
              <table width="100%" height="500px" border="0" cellpadding="10">
                <tbody>
                <tr>
                  <td width="100px">:</td>
                  <td>{{tableData.name}}</td>
                </tr>
                <tr>
                  <td>:</td>
                  <td>{{tableData.target}}</td>
                </tr>
                <tr>
                  <td>IDIIL:</td>
                  <td>
                    <div  v-if="sourceData.length>0">
                      <li class="resoucelist"
                          v-for="item1 in sourceData">
                        {{item1.name}}
                      </li>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td>:</td>
                  <td>
                    <div v-if="otherSource.length>0">
                      <li class="resoucelist"
                          v-for="item2 in otherSource">
                        <span>{{item2.name}}</span>
                        <a :href="'/' + item2.response.file" :download="item2.name"></a>
                      </li>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td>:</td>
                  <td>{{tableData.commits}}</td>
                </tr>
                </tbody>
              </table>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8" style="height: 100%;" class="righttable">
        </el-col>
      </el-row>
      <el-dialog title="" :visible.sync="dialogEditVisible" @close="closeDialogHandle" v-if='dialogEditVisible'>
        <add-class :data="tableData" type="update" key="update"></add-class>
      </el-dialog>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
  import headTop from '../../components/headTop'
  import addClass from './addClass'
  import {setStore,getStore} from '../../config/publicMethod'
  import {toJson} from '../../config/methods'
  import {writeFileJson,doTestLogin} from '../../api/user'
  import {getClassList,updateDoingClassInfo,deleteClassListInfo} from '../../api/classes'
  import classData from '../../data/classlist'
  export default {
    data(){
      let classList = JSON.parse(getStore("classList"));
      return {
        classList:classList,
        tableData:classList.length>0 ? classList[0] : classData.classList,
        currentindex:0,//
        isCollapse: false,
        dialogEditVisible:false,
        formLabelWidth: '90px',
        datetimes:''
      }
    },
    components:{
      headTop,
      addClass
    },
    activated() {
        //  vuekeep-alive,
//      this.getClassListInfo()
    },
    mounted(){
      $('.el-card__header').css('backgroundColor','#67c4ed');
      this.getClassListInfo()
    },
    computed: {
      sourceData() {
        let source = toJson(this.tableData.source);
        return source;
      },
      otherSource() {
        let othersource = this.tableData.otherSource?this.tableData.otherSource:this.tableData.othersource
        let othersource2 = toJson(othersource);
        return othersource2;
      },
    },
    methods: {
      async getClassListInfo(){
        let userInfo = JSON.parse(getStore("userInfo"));
        let classList2 = await getClassList({teacherId:userInfo.userId});//
        console.log("---classList2----->>",classList2)
        if(classList2.data.length>0){
          this.classList = classList2.data
          this.tableData = classList2.data[this.currentindex];
        }
        console.log("this.tableData--->>",this.tableData)
      },
      toJson:function(str){
        return toJson(str)
      },
      closeDialogHandle() {
        console.log("closeDialogHandle");
        //
        this.getClassListInfo()
      },
      clickItem(item,index){
        console.log(item,index)
        this.currentindex = index;
        this.tableData = this.classList[index]
      },
      async goToClass(tableData){
        console.log("",tableData);
        const result1 = await updateDoingClassInfo(tableData);//
        if(result1.code == 200){
          if(result1.type == '1'){
            this.$alert(" "+result1.data[0].name+" !", '');
            return
          }
        }
        //JSON
        const result = await writeFileJson(tableData);
        if(result.code == 200){
          console.log("---writeFileJson--->",result.data)
        }else {
          console.error("---writeFileJson-- ->",result)
          return
        }
        //json
        setStore("ClassUserList",result.data);
        let dataParams = {};
        dataParams.teacherId = JSON.parse(getStore('userInfo')).userId;
        dataParams.centerId = result.data.CenterID;
        dataParams.performanceID = result.data.classList.courseId;//ID
        let url = 'https://nwprodsub.idiil.com.cn/SYSTEM/MathInteractive/OnlineBuildDataDeal/OnlineBuildDataDeal.jsp?InstructorID='+dataParams.teacherId+'&sCenterID='+dataParams.centerId+'&sPerformanceID='+dataParams.performanceID;
        console.warn("url-->",url)
        window.open(url)
      },
      //
      deleteCourseConfirm(tableData,index){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteCourse(tableData,index);
          //
          this.currentindex = 0;
          this.getClassListInfo()
        }).catch(() => {
          this.$message({type: 'info', message: ''});
        });
      },
      async deleteCourse(tableData,index){
        console.log("",tableData,index)
        const result = await deleteClassListInfo(tableData);//
        if(result.code == 200){
          this.classList.splice(index,1);
          this.$message({type: 'success', message: '!'});
        }
      },
    }
  }
</script>

<style scoped lang="less" type="text/less">
  .classManagerList_section {
    padding: 20px;
    margin-bottom: 40px;
    .resoucelist{
      text-decoration: underline;
      margin: 5px 0;
    }
    .righttable table{
      border: 1px solid #999;
      tr td{
        border-top: 0;
        border-right: 1px solid #999;
        border-bottom: 1px solid #999;
        border-left: 0;

      }
    }
    .text {
      font-size: 14px;
      text-align: left;
    }
    .classlist{
      padding: 7px 4px;
      cursor: pointer;
      border-radius: 2px;
    }
    .item {
      margin-bottom: 5px;
    }
    .car_header{
      background-color: #67c4ed;
    }
    .listcolor{
      background-color: #ebeece;
    }
    .clearfix:before,
    .clearfix:after {
      display: table;
      content: "";
    }
    .clearfix:after {
      clear: both
    }

    .box-card {
      width: 100%;
    }
  }
</style>



